@import url(https://fonts.googleapis.com/css2?family=Anton&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Jaldi&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Russo+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Architects+Daughter&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Sora&display=swap);
html {
  scroll-behavior: smooth;
}
body {
  background-color: #fcf9ea;
  margin: 0;
}

.main-container {
  padding: 150px;
}
footer {
  display: flex;
  justify-content: center;
  background-color: #6d9886;
  padding: 20px;
  font-family: 'Sora', sans-serif;
  color: #212121;
}

ul {
  list-style: none;
  display: flex;
  padding: 0px;
  justify-content: space-evenly;
}

.section {
  margin: 100px auto;
}
h2 {
  font-family: 'Anton', sans-serif;
  margin-bottom: 40px;
  font-size: 35px;
}
p {
  color: #4a403a;
  font-family: 'Nunito', sans-serif;
}
.underline {
  -webkit-text-decoration: underline 15px #6d9886;
          text-decoration: underline 15px #6d9886;
}
h3 {
  font-family: 'Spartan', sans-serif;
  font-size: 20px;
}
.inner-section {
  background-color: #d9cab3;
  padding: 40px;
  border-radius: 10px;
  margin: 0 auto;
}
a {
  text-decoration: none;
  color: #6d9886;
  font-family: 'Nunito', sans-serif;
  font-weight: bolder;
}
a:hover {
  color: #f4a442;
}
.welcome {
  color: #f4a442;
  font-family: 'Architects Daughter', cursive;
  font-size: 30px;
}

@media screen and (max-width: 700px) {
  .header {
    flex-direction: column;
    margin: 0 auto;
    align-items: center;
  }
  .main-container {
    padding: 50px 10px 0px 10px;
  }
}
@media screen and (max-width: 600px) {
  .scroll-bar {
    height: 320px;
    width: 300px;
    overflow: auto;
  }
  .main-container {
    padding: 50px 5px 0px 5px;
  }
}

nav {
  background-color: #212121;
  padding: 5px;
  margin: none;
  font-family: 'Sora', sans-serif;
  font-size: 17px;
}

.projects {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.project {
  margin: 10px;
  min-width: 170px;
  padding: 20px;
  border-radius: 5px;
  background-color: #fff8e5;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  text-align: center;
}
.project:hover {
  opacity: 0.8;
}
.projects.icon {
  font-size: 20px;
}
.deployment {
  max-width: 100px;
  display: flex;
  justify-content: space-around;
  font-size: 20px;
  margin: 0 auto;
}
@media screen and (max-width: 580px) {
  .projects {
    width: 300px;
  }
}

.technologies {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.icon {
  font-size: 45px;
  padding: 10px;
  margin: 15px;
  color: #212121;
}
.icon:hover {
  color: #f4a442;
}

@media screen and (max-width: 580px) {
  .tech-bar {
    width: 300px;
  }
}

.contact-icon {
  margin: 5px;
  font-size: 20px;
}
.contact-links {
  font-family: 'Nunito', sans-serif;
  display: flex;
  margin: 10px;
  padding: 5px;
  justify-content: space-around;
  align-items: center;
}
@media screen and (max-width: 580px) {
  .contact-section {
    width: 300px;
  }
}

.header {
  display: flex;
  justify-content: center;
}
img {
  margin-top: 100px;
}

.typewriter {
  font-family: 'Russo One', sans-serif;
  display: inline-block;
}
.typewriter-text {
  display: inline-block;
  overflow: hidden;
  letter-spacing: 3px;
  animation: typing 5s steps(34, end), blink 0.75s step-end infinite;
  white-space: nowrap;
  font-size: 60px;
  font-weight: 700;
  border-right: 4px solid #f4a442;
  box-sizing: border-box;
}

.animate__animated.animate__slideInRight {
  --animate-duration: 3s;
}

@keyframes typing {
  from {
    width: 0%;
  }
  to {
    width: 100%;
  }
}

@keyframes blink {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: #f4a442;
  }
}

.institute {
  width: 120px;
  margin-top: 20px;
}
.education-section {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.education {
  margin: 10px;
  min-width: 240px;
  padding: 20px;
  border-radius: 5px;
  background-color: #fff8e5;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  text-align: center;
}
.education:hover {
  opacity: 0.9;
}
@media screen and (max-width: 580px) {
  .education-section {
    width: 300px;
  }
}

